import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Checkbox, Row, Col, Typography, message } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { login } from '../api';
import { useNavigate } from 'react-router-dom';

const { Title } = Typography;

const Login = () => {
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();


    useEffect(() => {
        document.title = "ng Nhp"; 
    }, []);

    const onFinish = async (values) => {
        setLoading(true);

        try {
            const response = await login(values);

            if (response.data.success) {
                console.log(response.data);
                message.success('ng nhp thnh cng!');

                // Cp nht thng tin vo RV_CONFIGS
                window.RV_CONFIGS.user_id = response.data.data.user_id;
                window.RV_CONFIGS.user_login = response.data.data.user_login;
                window.RV_CONFIGS.is_login = true;

                // Lu thng tin vo localStorage
                localStorage.setItem('is_login', true);
                localStorage.setItem('user_id', response.data.data.user_id);
                localStorage.setItem('user_login', response.data.data.user_login);

                // iu hng n trang Profile
                navigate('/profile', { replace: true });
                
                // Reload li trang
                window.location.reload();
            } else {
                message.error(response.data.data || 'ng nhp tht bi.');
            }
        } catch (error) {
            message.error(`C li xy ra: ${error.message}`);
        }

        setLoading(false);
    };

    const handleForgotPassword = () => {
        message.info('Hy truy cp vo trang qun mt khu ca chng ti.'); 
        // Bn c th chuyn hng ngi dng n trang qun mt khu ti y
    };

    return (
        <Row justify="center" style={{ marginTop: '50px' }}>
            <Col xs={24} sm={20} md={16} lg={8} xl={6}>
                <Title level={2} style={{ textAlign: 'center', marginBottom: '20px' }}>
                    ng Nhp
                </Title>
                <Form
                    name="login"
                    onFinish={onFinish}
                    layout="vertical"
                    initialValues={{ remember: true }}
                >
                    <Form.Item
                        name="username"
                        rules={[{ required: true, message: 'Vui lng nhp Email hoc s in thoi!' }]}
                    >
                        <Input 
                            prefix={<UserOutlined />} 
                            placeholder="Email hoc s in thoi " 
                            size="large" 
                        />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}
                    >
                        <Input.Password 
                            prefix={<LockOutlined />} 
                            placeholder="Mt khu" 
                            size="large" 
                        />
                    </Form.Item>
                    <Form.Item>
                        <Row justify="space-between">
                            <Col>
                                <Form.Item name="remember" valuePropName="checked" noStyle>
                                    <Checkbox>Ghi nh ti khon</Checkbox>
                                </Form.Item>
                            </Col>
                            <Col>
                                <Button type="link" onClick={handleForgotPassword}>
                                    Qun mt khu?
                                </Button>
                            </Col>
                        </Row>
                    </Form.Item>
                    <Form.Item>
                        <Button 
                            type="primary" 
                            htmlType="submit" 
                            block 
                            size="large" 
                            loading={loading}
                        >
                            ng nhp
                        </Button>
                    </Form.Item>
                </Form>
            </Col>
        </Row>
    );
};

export default Login;
