import React, { useState, useEffect } from 'react';
import { Form, Input, Button, Row, Col, Typography, Card, Tooltip, DatePicker, Select, message } from 'antd';
import { UserOutlined, IdcardOutlined, HomeOutlined, BankOutlined, BranchesOutlined, EditOutlined, CloseOutlined,MailOutlined,PhoneOutlined } from '@ant-design/icons';
import dayjs from 'dayjs';
import { getUserDetail } from '../api';
const { Title } = Typography;
const { Option } = Select;

const occupations = [
    'Cng nhn', 'Nhn vin vn phng', 'K s', 'Gio vin', 'Bc s', 'Lut s', 'Nng dn', 'Lp trnh vin', 
    'Thit k  ha', 'K ton', 'Nh qun l', 'Nh bo', 'Chuyn vin t vn', 'Th xy', 
    'Nhn vin bn hng', 'Sinh din', 'u bp', 'Th in', 'Th c kh', 'Khc'
];

const Profile = () => {
    const [form] = Form.useForm();
    const [isEditing, setIsEditing] = useState(false);
    const [userData, setUserData] = useState({});
    const [isOtherOccupation, setIsOtherOccupation] = useState(false);
    useEffect(() => {
        const fetchUserData = async () => {
            try {
                const response = await getUserDetail({ user_id: localStorage.getItem('user_id')})                
                console.log(response)
                if (response.data.success) {
                    const data = response.data.data;
                    setUserData({
                        ...data,
                dob: dayjs(data.dob, 'YYYY-MM-DD').format('DD/MM/YYYY'),
                identityIssueDate: data.identityIssueDate ? dayjs(data.identityIssueDate, 'YYYY-MM-DD').format('DD/MM/YYYY') : dayjs().format('DD/MM/YYYY'),
                identityIssuePlace:"updated",
                    });                    
                    form.setFieldsValue({
                        ...data,
                        birthdate: dayjs(data.dob, 'YYYY-MM-DD'),
                        identityIssueDate: data.identityIssueDate ? dayjs(data.identityIssueDate, 'YYYY-MM-DD') : dayjs(),
                        identityIssuePlace:"Update later",
                        permanentAddress:"Update later",
                        occupation:"Update later"
                    });
                } else {
                    message.error('Khng th ti thng tin ngi dng.');
                }
            } catch (error) {
                message.error('Li khi gi API.');
            }
        };

        fetchUserData();
    }, [form]);

    const onFinish = (values) => {
        const birthdate = values.birthdate ? dayjs(values.birthdate) : null;
        const identityIssueDate = values.identityIssueDate ? dayjs(values.identityIssueDate) : null;

        if (!birthdate || !birthdate.isValid()) {
            message.error('Ngy thng nm sinh khng hp l.');
            return;
        }

        if (!identityIssueDate || !identityIssueDate.isValid()) {
            message.error('Ngy cp khng hp l.');
            return;
        }

        const formattedValues = {
            ...values,
            birthdate: birthdate.format('DD/MM/YYYY'),
            identityIssueDate: identityIssueDate.format('DD/MM/YYYY'),
        };

        console.log('Updated values:', formattedValues);
        message.success('Thng tin  c cp nht thnh cng!');
        setIsEditing(false);
    };

    const handleEditClick = () => {
        setIsEditing(!isEditing);
    };

    const handleCancelEdit = () => {
        setIsEditing(false);
        form.setFieldsValue({
            ...userData,
            birthdate: userData.dob ? dayjs(userData.dob, 'DD/MM/YYYY') : dayjs(),
            identityIssueDate: userData.identityIssueDate ? dayjs(userData.identityIssueDate, 'DD/MM/YYYY') : dayjs(),
        });
        setIsOtherOccupation(false);
    };

    const handleOccupationChange = (value) => {
        if (value === 'Khc') {
            setIsOtherOccupation(true);
            form.setFieldsValue({ occupation: 'Khc' });
        } else {
            setIsOtherOccupation(false);
        }
    };

    return (     
        <div>
            <Title level={2} style={{ textAlign: 'center', marginBottom: '20px' }}>
                Thng tin ti khon
            </Title>
            <Row justify="center" gutter={16} style={{ marginTop: '50px' }}>                            
    <Col xs={24} sm={20} md={10} lg={8}>
        <Card title="Thng Tin C Nhn" extra={
            !isEditing ? (
                <Button icon={<EditOutlined />} onClick={handleEditClick}>Chnh sa</Button>
            ) : (
                <Button icon={<CloseOutlined />} onClick={handleCancelEdit}>Hy</Button>
            )
        }>
            <Form
                form={form}
                name="profile"
                onFinish={onFinish}
                layout="vertical"
                initialValues={userData}
            >
                <Form.Item label="M s ngi gii thiu">
                    <Tooltip title={userData.referrer_name}>
                        <Input prefix={<UserOutlined />} value={userData.parent_id} disabled />
                    </Tooltip>
                </Form.Item>
                <Form.Item label="M s nhn vin">
                    <Input prefix={<IdcardOutlined />} value={userData.user_id} disabled />
                </Form.Item>
                <Form.Item
                    label="H v tn"
                    name="fullName"
                    rules={[{ required: true, message: 'Vui lng nhp h v tn!' }]}
                >
                    <Input prefix={<UserOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Email"
                    name="email"
                    rules={[{ required: true, type: 'email', message: 'Vui lng nhp email hp l!' }]}
                >
                    <Input prefix={<MailOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="S in thoi"
                    name="user_phone"
                    rules={[{ required: true, message: 'Vui lng nhp s in thoi!' }]}
                >
                    <Input prefix={<PhoneOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Ngy thng nm sinh"
                    name="birthdate"
                    rules={[{ required: true, message: 'Vui lng nhp ngy thng nm sinh!' }]}
                >
                    <DatePicker format="DD/MM/YYYY" style={{ width: '100%' }} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="S CCCD/CMND"
                    name="id_number"
                    rules={[{ required: true, message: 'Vui lng nhp s CCCD/CMND!' }]}
                >
                    <Input prefix={<IdcardOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Ngy cp"
                    name="identityIssueDate"
                    rules={[{ required: true, message: 'Vui lng nhp ngy cp!' }]}
                >
                    <DatePicker format="DD/MM/YYYY" style={{ width: '100%' }} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Ni cp"
                    name="identityIssuePlace"
                    rules={[{ required: true, message: 'Vui lng nhp ni cp!' }]}
                >
                    <Input prefix={<IdcardOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="a ch thng tr"
                    name="permanentAddress"
                    rules={[{ required: true, message: 'Vui lng nhp a ch thng tr!' }]}
                >
                    <Input prefix={<HomeOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Ngh nghip"
                    name="occupation"
                    rules={[{ required: false, message: 'Vui lng chn ngh nghip!' }]}
                >
                    <Select
                        showSearch
                        placeholder="Chn ngh nghip"
                        optionFilterProp="children"
                        disabled={!isEditing}
                        onChange={handleOccupationChange}
                    >
                        {occupations.map(occupation => (
                            <Option key={occupation} value={occupation}>{occupation}</Option>
                        ))}
                    </Select>
                </Form.Item>
                {isOtherOccupation && (
                    <Form.Item
                        label="Nhp ngh nghip"
                        name="otherOccupation"
                        rules={[{ required: false, message: 'Vui lng nhp ngh nghip!' }]}
                    >
                        <Input disabled={!isEditing} />
                    </Form.Item>
                )}
            </Form>
        </Card>
    </Col>
    <Col xs={24} sm={20} md={10} lg={8}>
        <Card title="Thng Tin Ngn Hng">
            <Form
                form={form}
                name="bankInfo"
                layout="vertical"
                initialValues={userData}
            >
                <Form.Item
                    label="S ti khon ngn hng"
                    name="bank_account_number"
                    rules={[{ required: true, message: 'Vui lng nhp s ti khon ngn hng!' }]}
                >
                    <Input prefix={<BankOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Tn ngn hng"
                    name="bank_name"
                    rules={[{ required: true, message: 'Vui lng nhp tn ngn hng!' }]}
                >
                    <Input prefix={<BankOutlined />} disabled={!isEditing} />
                </Form.Item>
                <Form.Item
                    label="Chi nhnh ngn hng"
                    name="bank_branch"
                    rules={[{ required: true, message: 'Vui lng nhp chi nhnh ngn hng!' }]}
                >
                    <Input prefix={<BranchesOutlined />} disabled={!isEditing} />
                </Form.Item>
                {isEditing && (
                    <Form.Item>
                        <Button type="primary" htmlType="submit" block>
                            Lu thng tin
                        </Button>
                    </Form.Item>
                )}
            </Form>
        </Card>
    </Col>
</Row>

        </div>   
    );
};

export default Profile;
